export default {
	name: 'cnav',

	props: {
		bg: {
			type: String,
			default: '#000000'
		},
		text: {
			type: String,
			default: ''
		},

		showLeft: {
			type: Boolean,
			default: true
		},

		leftIcon: {
			type: String,
			default: '/static/back-white.png'
		},

		cleft: {
			type: Boolean,
			default: false
		},

		showRight: {
			type: Boolean,
			default: true
		},

		rightIcon: {
			type: String,
			default: '/static/reload.png'
		},

		cright: {
			type: Boolean,
			default: false
		}
	},
	methods: {
		cleftClick() {
			if (this.cleft) {
				this.$emit('leftClick')
			} else {
				uni.navigateBack({

				})
			}
		},
		crightClick() {
			if (this.cright) {
				this.$emit('rightClick')
			}
		}
	}
}
